export default class RegistrationForm {
    //Constructor
    constructor() {
        this.allFields = document.querySelectorAll("#registration-form .form-control")
        this.insertValidationElements()
        this.username = document.querySelector("#username-register")
        this.username.previousValue = ""
        this.events()
    }

    //Events
    events() {
        this.username.addEventListener("keyup", () => {
            this.isDifferent(this.username, this.usernameHandler)
        })
    }

    usernameHandler() {
        this.usernameImmediately()
        clearTimeout(this.username.timer)
        this.username.timer = setTimeout( () => this.usernameAfterDelay(), 3000)
    }

    usernameImmediately() {
        console.log("Immediate method just ran")
    }

    usernameAfterDelay() {
        alert("After delay method just ran")
    }

    isDifferent(el, handler) {
        if (el.previousValue != el.value) {
            //Keep the this keyword pointing towards the current element
            handler.call(this)
        }
        el.previousValue = el.value
    }


    //Methods
    insertValidationElements() {
        this.allFields.forEach( el => {
            el.insertAdjacentHTML('afterend', '<div class="alert alert-danger small liveValidateMessage"></div>')
        })
    }
}